* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #b5c0ad;
}

.LoginScreen {
  width: calc(100% - 10rem);
  margin: 10%;
  background: #fff;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.LoginScreen > form {
  background: #fff;
}
.inputGroup {
  margin-top: 5%;
  background: #fff;
}
.inputGroup > label,
.inputGroup > input {
  font-family: "Roboto", sans-serif;
  text-align: left;
  margin: 1%;
}
.inputGroup > label {
  color: #676767;
  display: flex;
  background: #fff;
}
.inputGroup > input {
  background: lightgray;
  height: 2rem;
  width: 30rem;
  display: flex;
  border-radius: 0.3rem;
  padding: 0 1rem;
}
.inputGroup > input:focus {
  color: black;
}
.inputGroup > input::placeholder {
  color: #c4c4c4;
}
.loginButton {
  margin: 10%;
  background: #f58733;
  width: 50%;
  font-size: 2rem;
  border-radius: 0.5rem;
}
.loginButton:hover {
  background: #ea6c0b;
}
